section#partiesLigues {
    height: 100%;
    display: flex;
    justify-content: center;
}

#partiesLigues > ul {
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

li.infoParties {
    background-color: #fff;
    box-shadow: 0 0 4px rgba(0, 0, 0.5);
    width: 90%;
    height: 6em;
    margin: 1em 0;
    border-radius: 1em;
    padding: 1em;
    display: flex;
    align-items: center;
}

div.informations {
    display: flex;
    align-items: center;
    flex-grow: 1;
    background: #fff;
}

span.date,
span.lieu {
    display: block;
    text-align: center;
    padding: 5px;
}

span.equipeDom,
span.equipeVis {
    display: block;
    text-align: center;
    padding: 5px;
}

span.date {
    margin-right: 10px;
}

span.lieu,
span.equipeDom,
span.equipeVis {
    margin: 0px;
}

button.details {
    margin-left: auto;
}

div.barreVerticale {
    border-left: 4px solid rgba(64, 72, 88, 0.4); 
    border-radius: 15%;
    height: 5em;
    margin: 0 10px; 
    min-height: 1em; 
}

section#ligues button{
    width: 10%;
    height: 15%;
    border:1px solid gray;
    padding:10px;
    text-align: center;
    border-radius: 0.5rem;
    cursor: pointer;
    background-color: white;
    color:black;
    margin-left: 10px;
}

#ligues{
    display: flex;
}

#ligues button:hover{
    background-color: #93c1f3;
}

#ligues button.ligueActive{
    background-color:#519def;
}